export default {
	props:{
		item : Object,
		index : Number
	},
	methods:{
		// 进入详情页
		Details(){
			console.log("进入详情页")
		},
		/* 关注按钮方法 */
		onFocus(){
			this.item.isguanzhu = true;
			uni.showToast({
				title:"关注成功!"
			});
		},
		// 取消关注按钮方法
		onChanl(){
			uni.showModal({
			    title: '提示',
			    content: '是否取消关注',
			    success: (res)=> {
			        if (res.confirm) {
						this.item.isguanzhu = false;
						uni.showToast({
							title:"取消成功!"
						});
			        }
			    }
			});
		},
		// 顶和踩事件
		dingcai(type){
			switch (type){
				case 1: 
				if(this.item.infobar.isexpress == 1){return ;}
				this.item.infobar.zannum++
				if(this.item.infobar.isexpress == 2)
				this.item.infobar.cainum--
				this.item.infobar.isexpress =1;
					break;
				case 2:
				if(this.item.infobar.isexpress == 2){return ;}
				this.item.infobar.cainum++
				if(this.item.infobar.isexpress == 1)
				this.item.infobar.zannum--
				this.item.infobar.isexpress =2;
					break;
			}
		}
	}
}
